    transition: .2s;
}

.doboard_task_widget-switch input:checked + .doboard_task_widget-slider {
    background-color: #65D4AC;
}

.doboard_task_widget-switch input:checked + .doboard_task_widget-slider:before {
    transform: translateX(20px);
}

.doboard_task_widget-switch-img {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
}

.doboard_task_widget-switch-center {
    display: flex;
    gap: 2px;
    flex-direction: column;
    -moz-flex-direction: column;
    flex: 1 1 auto;
    min-width: 0;
}

.doboard_task_widget-switch-desc {
    display: block;
    font-size: 12px;
    color: #707A83;
    margin: 0;
    line-height: 1.2;
    max-width: 180px;
    word-break: break-word;
}

/*** \`COMMENTS\` INTERFACE ***/

.doboard_task_widget-concrete_issue-day_content {
    display: flex;
    flex-direction: column;
    -moz-flex-direction: column;
}

.doboard_task_widget-concrete_issue_day_content-month_day {
    text-align: center;
    font-weight: 400;
    font-size: 12px;
    line-height: 100%;
    padding: 8px;
    opacity: 0.75;
}

.doboard_task_widget-concrete_issue_day_content-messages_wrapper {
    display: flex;
    flex-direction: column;
    -moz-flex-direction: column;
}

.doboard_task_widget-comment_data_wrapper {
    display: flex;
    flex-direction: row;
    -moz-flex-direction: row;
    margin-bottom: 15px;
    align-items: flex-end;
}

.doboard_task_widget-comment_text_container {
    position: relative;
    width: calc(100% - 44px - 5px);
    height: auto;
    margin-left: 5px;
    background: #F3F6F9;
    border-radius: 16px;
}
.doboard_task_widget-comment_text_container:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -5px;
    width: 13px;
    height: 19px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}
.doboard_task_widget-comment_data_owner .doboard_task_widget-comment_text_container {
    background: #EBFAF4;
}
.doboard_task_widget-comment_data_owner .doboard_task_widget-comment_text_container:after {
    left: auto;
    right: -5px;
    height: 13px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.doboard_task_widget-comment_body,
.doboard_task_widget-comment_time {
    position: relative;
    z-index: 1;
}

.doboard_task_widget-comment_body {
    padding: 6px 8px;
    min-height: 30px;
}

.doboard_task_widget-comment_body strong {
    font-variation-settings: "wght" 700;
}

.doboard_task_widget-comment_body blockquote {
    margin: 0;
    border-left: 3px solid #22a475;
}

.doboard_task_widget-comment_body blockquote p {
    margin: 0 10px;
}

.doboard_task_widget-comment_body details .mce-accordion-body {
    padding-left: 20px;
}

.doboard_task_widget-comment_body details .mce-accordion-summary {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_3___}) no-repeat 0;
    padding-left: 20px;
}

.doboard_task_widget-comment_body .mce-accordion[open] .mce-accordion-summary {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_4___}) no-repeat 0;
}

.doboard_task_widget-comment_body details .mce-accordion-summary::marker {
    content: "";
}

.doboard_task_widget-comment_body pre {
    border: 1px solid #d6dde3;
    border-left-width: 8px;
    border-radius: 4px;
    padding: 13px 16px 14px 12px;
    white-space: pre-wrap;
}

.doboard_task_widget-comment_time {
    font-weight: 400;
    font-size: 11px;
    opacity: .8;
    position: absolute;
    bottom: 6px;
    right: 6px;
}

.doboard_task_widget-comment_body-img-strict {
    max-width: -webkit-fill-available;
    height: 100px;
    margin-right: 5px;
}

/** SENDING NEW COMMENT **/

.doboard_task_widget-send_message {
    padding: 14px 10px;
    border-top: 1px solid #BBC7D1;
    position: sticky;
    background: white;
    bottom: 0;
    z-index: 4;
}

.doboard_task_widget-send_message_elements_wrapper {
    display: flex;
    flex-direction: row;
    -moz-flex-direction: row;
    align-content: center;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: end;
}


.doboard_task_widget-send_message_elements_wrapper img {
    margin: 0;
}

.doboard_task_widget-send_message_input_wrapper {
    position: relative;
    display: inline-grid;
    align-items: center;
    justify-items: center;
    flex-grow: 1;
    padding: 0 6px;
}

.doboard_task_widget-send_message_input_wrapper textarea {
    position: relative;
    width: 100%;
    height: 37px;
    border: none;
    outline: none;
    box-shadow: none;
    border-radius: 24px;
    background: #F3F6F9;
    resize: none;
    margin-bottom: 0 !important;
    transition: height 0.2s ease-in-out;
    padding: 8px;
    box-sizing: border-box;
}
.doboard_task_widget-send_message_input_wrapper textarea.high {
    height: 170px;
}

.doboard_task_widget-send_message_input_wrapper textarea:focus {
    background: #F3F6F9;
    border-color: #007bff;
    outline: none;
}

.doboard_task_widget-send_message_button {
    display: inline-grid;
    border: none;
    cursor: pointer;
    padding: 0;
    width: 100%;
    align-items: center;
    margin: 20px 0 0 0;
}
.doboard_task_widget-send_message_paperclip {
    display: inline-grid;
    border: none;
    background: none;
    cursor: pointer;
    padding: 0;
    align-items: center;
    margin: 0;
}

.doboard_task_widget-send_message_button:hover, .doboard_task_widget-send_message_paperclip:hover rect {
    fill: #45a049;
}

.doboard_task_widget-send_message_button:active, .doboard_task_widget-send_message_paperclip:active {
    transform: scale(0.98);
}

.doboard_task_widget-spinner_wrapper_for_containers {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 60px;
    width: 100%;
}

.doboard_task_widget-spinner_for_containers {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: conic-gradient(transparent, #1C7857);
    mask: radial-gradient(farthest-side, transparent calc(100% - 4px), #fff 0);
    animation: spin 1s linear infinite;
}

.doboard_task_widget-create_issue {
    padding: 10px;
}

/*fileuplaod*/

.doboard_task_widget__file-upload__wrapper {
    display: none;
    border: 1px solid #BBC7D1;
    margin-top: 14px;
    padding: 0 10px 10px;
    border-radius: 4px;
}

.doboard_task_widget__file-upload__list-header {
    text-align: left;
    font-size: .9em;
    margin: 5px 0;
    color: #444c529e;
}

.doboard_task_widget__file-upload__file-input-button {
    display: none;
}

.doboard_task_widget__file-upload__file-list {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 6px;
    max-height: 200px;
    overflow-y: auto;
    background: #f3f6f9;
}

.doboard_task_widget__file-upload__file-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px;
    border-bottom: 1px solid #bbc7d16b;
}

.doboard_task_widget__file-upload__file-item:last-child {
    border-bottom: none;
}

.doboard_task_widget__file-upload__file_info {
    display: inline-flex;
    align-items: center;
}

.doboard_task_widget__file-upload__file-name {
    font-weight: bold;
    font-size: 0.9em;
}

.doboard_task_widget__file-upload__file-item-content {
    width: 100%;
}

.doboard_task_widget__file-upload__file_size {
    color: #666;
    font-size: 0.8em;
    margin-left: 6px;
}

.doboard_task_widget__file-upload__remove-btn {
    background: #22a475;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.doboard_task_widget__file-upload__error {
    display: block;
    margin: 7px 0 0;
    padding: 7px;
    border-radius: 4px;
    background: #ffdddd;
    border: 1px solid #cf6868;
}

.doboard_task_widget-show_button {
    position: fixed;
    background: #1C7857;
    color: #FFFFFF;
    padding: 8px 12px;
    border-radius: 4px;
    font-size: 14px;
    z-index: 10000;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0,0,0,0.3);
    transform: translate(-50%, -100%);
    margin-top: -8px;
    white-space: nowrap;
    border: none;
    font-family: inherit;
}

/*fileuplaod*/

@keyframes spin {
    to { transform: rotate(1turn); }
}

@media (max-width: 480px) {
    .doboard_task_widget {
        position: fixed;
        right: 0;
        top: auto;
        bottom: 0;
        margin: 0 20px 20px;
        box-sizing: border-box;
        transform: translateZ(0);
        -moz-transform: translateZ(0);
        will-change: transform;
        max-height: 90vh;
    }

    .doboard_task_widget-header {
        padding: 8px;
    }

    .doboard_task_widget-issue-title {
        max-width: 70px;
    }

    .doboard_task_widget-container {
        width: 100%;
        max-width: 290px;
        min-width: 290px;
        margin: 0 auto;
        max-height: 90vh;
    }

    .doboard_task_widget-container-maximize {
        width: 100%;
        max-width: 290px;
        margin: 0 auto;
        max-height: 90vh;
    }

    .doboard_task_widget-content{
        height: auto;
        max-height: 100%;
        min-height: 100%;
        scrollbar-width: none;
    }

    .doboard_task_widget-content::-webkit-scrollbar {
        display: none;
    }

    .doboard_task_widget-all_issues-container, .doboard_task_widget-concrete_issues-container {
        max-height: 80vh;
    }
}

@supports (-webkit-overflow-scrolling: touch) {
    .doboard_task_widget {
        position: fixed;
    }
}

.doboard_task_widget_tasks_list {
    background-color: white;
    position: sticky;
    bottom: 0px;
    height: 38px;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 8px;
}

#doboard_task_widget-user_menu-logout_button {
    display: inline-flex;
    align-items: center;
}

.doboard_task_widget-text_selection {
    position: relative;
    display: inline-block;
}

.doboard_task_widget-see-task {
    cursor: pointer;
    text-decoration: underline;
}

.doboard_task_widget-text_selection_tooltip {
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    background: #FFFFFF;
    color: #000000;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 10px;
    white-space: nowrap;
    z-index: 9000;
    border: 1px solid #BBC7D1;
    margin-bottom: 8px; /*    */
}

.doboard_task_widget-text_selection_tooltip::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border: 5px solid transparent;
    border-top-color: #FFFFFF; /*    */
}

.doboard_task_widget-text_selection_tooltip::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border: 6px solid transparent;
    border-top-color: #BBC7D1; /*    */
    z-index: -1;
}

.doboard_task_widget-text_selection_tooltip_icon {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
    background-repeat: no-repeat;
    width: 22px;
    height: 22px;
    margin: 5px 3px;
}

.doboard_task_widget-text_selection_tooltip_element {
    display: flex;
    justify-content: space-between;
}

.doboard_task_widget-content .tox .tox-toolbar__group {
    padding: 0 8px !important;
}

.doboard_task_widget-content .tox-tinymce {
    border: 1px solid #BBC7D1;
    border-radius: 4px;
}

 .doboard_task_widget-content ul, .doboard_task_widget-content ol {
    margin: 0 0 0 1em !important;
}

.toggle {
    position: relative;
    display: inline-block;
    width: 46px;
    min-width: 46px;
    height: 28px;
}

.toggle input {
    opacity: 0;
    width: 0;
    height: 0;
    position: absolute;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0; left: 0; right: 0; bottom: 0;
    background-color: #bbc7d1;
    border-radius: 24px;
    transition: 0.3s;
}

.slider:before {
    content: "";
    position: absolute;
    height: 24px;
    width: 24px;
    left: 2px;
    top: 2px;
    background-color: white;
    border-radius: 50%;
    transition: 0.3s;
}

input:checked + .slider {
    background-color: #65d4ac;
}

input:checked + .slider:before {
    transform: translateX(16px);
}

.logout_button {
    font-weight: 500;
    font-size: 14px;
    color: #707A83;
    cursor: pointer;
}
.doboard_task_widget-show_login_form, .doboard_task_widget-on_phantom_login_page, .doboard_task_widget-forgot_password {
    display: inline-block;
    cursor: pointer;
    color: #2F68B7;
    margin-bottom: 0;
}

.doboard_task_widget-forgot_password {
     margin-bottom: 20px;
}

.doboard_task_widget-login-is-invalid {
    color: red;
}

.doboard_task_widget-input-container-login-menu,
.doboard_task_widget-forgot_password_form-menu{
    margin: 20px;
}


.doboard_task_widget-bottom-eye-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    background: #fff url(${___CSS_LOADER_URL_REPLACEMENT_6___}) no-repeat center center;
    background-size: 16px 16px;
    cursor: pointer;
    border-radius: 50%;
    transition: all 0.2s ease;
    z-index: 10;
}

.doboard_task_widget-bottom-eye-off-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    background: #fff url(${___CSS_LOADER_URL_REPLACEMENT_7___}) no-repeat center center;
    background-size: 16px 16px;
    cursor: pointer;
    border-radius: 50%;
    transition: all 0.2s ease;
    opacity: 0.5;
    z-index: 10;

}
.doboard_task_widget-input-container-textarea {
    position: relative;
    margin-top: 20px;
}

.doboard_task_widget-field-textarea-label {
    position: absolute;
    left: 10px;
    top: 10px;
    padding: 0 5px;
    color: #666;
    background-color: white;
    transition: 0.2s ease all;
    pointer-events: none;
}

.doboard_task_widget-input-container-textarea .doboard_task_widget-field ~ label {
    top: -10px;
    left: 14px;
    font-size: 10px;
