.doboard_task_widget-paperclip-tooltip::after {\n    content: \"\";\n    position: absolute;\n    left: 8%;\n    top: 100%;\n    transform: translateX(-50%);\n    pointer-events: none;\n    background: transparent;\n}\n\n.doboard_task_widget-send_message_paperclip .doboard_task_widget-paperclip-tooltip::after {\n    border-left: 8px solid transparent;\n    border-right: 8px solid transparent;\n    border-top: 8px solid #545b61;\n    display: block;\n}\n.doboard_task_widget-send_message_paperclip {\n    position: relative;\n}\n\n.doboard_task_widget-send_message_paperclip .doboard_task_widget-paperclip-tooltip {\n    display: none;\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    transform: translateX(-3%) translateY(-43px);\n    background: #545b61;\n    color: #FFF;\n    border: none;\n    border-radius: 3px;\n    padding: 10px 16px;\n    font-size: 13px;\n    line-height: 1.4;\n    z-index: 100;\n    min-width: 220px;\n    max-width: 320px;\n    text-align: left;\n    pointer-events: none;\n    text-transform: none;\n}\n\n.doboard_task_widget-send_message_paperclip:hover .doboard_task_widget-paperclip-tooltip {\n    display: block;\n}\n/** COMMON CLASSES **/\n\n.doboard_task_widget * {\n    font-family: Inter, sans-serif;\n    font-weight: 400;\n    font-size: 14px;\n    line-height: 130%;\n    color: #40484F;\n}\n.doboard_task_widget-header * {\n    color: #252A2F;\n    margin: 0;\n}\n\n.doboard_task_widget-header-icons {\n    display: flex;\n}\n\n.doboard_task_widget a {\n    text-decoration: underline;\n    color: #2F68B7;\n}\n.doboard_task_widget a:hover {\n    text-decoration: none;\n}\n\n.doboard_task_widget {\n    position: fixed;\n    right: 50px;\n    bottom: 20px;\n    z-index: 9999;\n    vertical-align: middle;\n    transition: top 0.1s;\n    transform: translateZ(0);\n    -webkit-transform: translateZ(0);\n    will-change: transform;\n}\n\n.doboard_task_widget_cursor-pointer {\n    cursor: pointer;\n}\n\n.doboard_task_widget-container-maximize #doboard_task_widget-description {\n    height: 90%;\n    position: absolute;\n}\n\n.doboard_task_widget-container-maximize #doboard_task_widget-description.is-small {\n    position: relative;\n    height: auto;\n}\n\n.doboard_task_widget-container-maximize .doboard_task_widget-field {\n    display: flex;\n    justify-content: center;\n}\n\n.doboard_task_widget-container-maximize {\n    width: 80vw !important;\n    max-width: 1120px !important;\n    max-height: 75vh !important;\n    min-height: 75vh !important;\n    display: flex;\n    flex-direction: column;\n    -moz-flex-direction: column;\n}\n\n.doboard_task_widget-container {\n    width: 430px;\n    max-height: calc(100vh - 40px);\n    display: flex;\n    flex-direction: column;\n    -moz-flex-direction: column;\n}\n\n.doboard_task_widget-header {\n    display: flex;\n    height: 41px;\n    min-height: 41px;\n    padding: 0px 16px;\n    /*background: #1C7857;*/\n    background-color: #EBF0F4;\n    border-radius: 8px 8px 0 0;\n    border: 1px solid #BBC7D1;\n    border-bottom: none;\n    justify-content: space-between;\n    align-items: center;\n    color: #FFFFFF;\n}\n\n.doboard_task_widget-user_menu-header {\n    display: flex;\n    padding: 16px 16px;\n    border: 1px #BBC7D1 solid;\n    border-bottom-color: #EBF0F4;\n    border-radius: 8px 8px 0 0;\n    flex-direction: column;\n    align-items: center;\n    color: #252A2F;\n    background-color: #F3F6F9;\n}\n\n.doboard_task_widget-user_menu-header-top {\n    display: flex;\n    height: fit-content;\n    align-items: center;\n    width: 100%;\n    justify-content: space-between;\n}\n\n.doboard_task_widget-user_menu-header-avatar {\n    max-width: 60px;\n    max-height: 60px;\n    width: 60px;\n    height: 60px;\n    border-radius: 50%;\n    margin-bottom: 4px\n}\n\n.doboard_task_widget-user_menu-item {\n    display: flex;\n    align-items: center;\n    border-bottom: 1px #EBF0F4 solid;\n    padding: 0 16px;\n    height: 40px;\n    margin-top: 10px;\n}\n\n.